html, body {
	margin: 0;
	padding: 0;
	background-image: -webkit-linear-gradient(#ccc, #738290);
	background-image: linear-gradient(#ccc, #738290);
	width: 100%;
	height: 100%;
	font-family: 'Hind', sans-serif;
}

#root {
	width: 100%;
	height: 100%;
}

p, span {
	color: #ccc;
	font-size: 18px;
}

h1, h3 {
	color: #fff;
}

h1 {
	font-family: 'Sorts Mill Goudy', serif;
	padding: 0 40px 10px 40px;
	border-bottom: 2px solid #eee;
}

h2 {
	font-family: 'Sorts Mill Goudy', serif;
	margin-bottom: 0;
	padding-bottom: 10px;
	border-bottom: 1px solid #333;
}

h4 {
	font-family: 'Sorts Mill Goudy', serif;
	font-size: 60px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	height: 100%;
	width: 100%;
}

a, a:hover, a:focus {
	color: #ccc;
}

i {
	padding-left: 8px;
	padding-right: 5px;
}

.WeatherInput {
	width: 100%;
	height: 100%;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	padding: 20px;
	min-height: 250px;
	border-bottom: 2px solid #102542;
}

.WeatherInput .row {
	height: 100%;
}

.input-group {
	/*box-shadow: 4px 4px 0 rgba(200,200,200,0.5);*/
	width: 100%;
	border-radius: 25px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: flex-end;
	        justify-content: flex-end;
	margin-bottom: 20px;
}

select {
	border-radius: 0 25px 25px 0;
	border: 0;
	color: #777;
	text-indent: 5px;
	padding-right: 15px;
}

input[type="text"],
select.form-control,
.input-group-text {
	border: 0;
	border-radius: 25px;
	box-sizing: border-box;
	color: #777;
}

input[type="text"]:focus,
select.form-control:focus,
.input-group-text:focus {
	box-shadow: none;
	color: #777;
}

.input-group-text {
	color: #fff;
	/*background-color: #da4167;*/
	background-color: #102542;
}

.search-result-container {
	width: 100%;
	height: 100%;
	min-height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	min-height: 150px;
}

.WeatherDayList {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	margin: 50px 0;
	width: 100%;
	color: #333;
}

.WeatherDayListNoResults {
	width: 100%;
	color: #333;
	text-align: center;
}

.WeatherDayListNoResults * {
	display: inline-block;
}

.WeatherDayListNoResults img {
	-webkit-filter: invert(100%);
	        filter: invert(100%);
}

.WeatherDayList p, .WeatherDayList h2 {
	color: #333;
}

.WeatherDay {
	width: 200px;
	display: inline-block;
	text-align: center;
	margin: 10px;
	padding: 20px;
	padding-bottom: 30px;
	background-color: #fff;
	border-radius: 5px;
	box-shadow: 0 0 8px #333;
}

.WeatherImage img {
	opacity: 0.75;
}

.tempContainer p {
	display: inline-block;
	margin-bottom: 0;
	background-color: #102542;
	padding: 6px 20px;
	border-radius: 50px;
	line-height: 1.0;
}

.tempContainer span:nth-child(1) {
	color: #999;
}

.tempContainer span:nth-child(2) {
	color: #ddd;
}

.tempContainer span:nth-child(3) {
	color: #ddd;
}

footer {
	position: fixed;
	bottom: 0;
	width: 100%;
	background-color: #102542;
	color: #ccc;
	text-align: center;
	padding: 2px;
}

.fade-in {
	-webkit-animation: fade-in-animation 1s;
	        animation: fade-in-animation 1s;
}

@-webkit-keyframes fade-in-animation {
	from { opacity: 0; }
    to   { opacity: 1; }
}

@keyframes fade-in-animation {
	from { opacity: 0; }
    to   { opacity: 1; }
}

.fade-in-and-drop {
	position: relative;
	-webkit-animation: fade-in-animation 1s;
	        animation: fade-in-animation 1s;
}

@keyframes fade-in-animation {
	from { opacity: 0; bottom: 50px; }
    to   { opacity: 1; bottom: 0;}
}

.slide-out {
	position: relative;
	-webkit-animation: slide-out-animation 1s;
	        animation: slide-out-animation 1s;
}

@-webkit-keyframes slide-out-animation {
	from { opacity: 1; top: 0; }
	to { opacity: 0; top: 30px; }
}

@keyframes slide-out-animation {
	from { opacity: 1; top: 0; }
	to { opacity: 0; top: 30px; }
}

.slide-in {
	position: relative;
	-webkit-animation: slide-in-animation 1s;
	        animation: slide-in-animation 1s;
}

@-webkit-keyframes slide-in-animation {
	from { opacity: 0; top: -30px; }
	to { opacity: 1; top: 0; }
}

@keyframes slide-in-animation {
	from { opacity: 0; top: -30px; }
	to { opacity: 1; top: 0; }
}







